.primary {
  color: var(--color-on-primary);
  background-color: var(--color-primary);
}

.on-primary {
  color: var(--color-primary);
  background-color: var(--color-on-primary);
}

.primary-container {
  color: var(--color-on-primary-container);
  background-color: var(--color-primary-container);
}

.on-primary-container {
  color: var(--color-primary-container);
  background-color: var(--color-on-primary-container);
}

.error-container {
  color: var(--color-on-error-container);
  background-color: var(--color-error-container);
}

.on-error-container {
  color: var(--color-error-container);
  background-color: var(--color-on-error-container);
}

.background {
  color: var(--color-on-background);
  background-color: var(--color-background);
}

.on-background {
  color: var(--color-background);
  background-color: var(--color-on-background);
}

.surface {
  color: var(--color-on-surface);
  background-color: var(--color-surface);
}

.on-surface {
  color: var(--color-surface);
  background-color: var(--color-on-surface);
}

.surface-variant {
  color: var(--color-on-surface-variant);
  background-color: var(--color-surface-variant);
}

.on-surface-variant {
  color: var(--color-surface-variant);
  background-color: var(--color-on-surface-variant);
}

.outline {
  color: var(--color-on-outline);
  background-color: var(--color-outline);
}

.on-outline {
  color: var(--color-outline);
  background-color: var(--color-on-outline);
}

body [data-color-scheme=light], body,
html [data-color-scheme=light], html {
  /* light main colors */
  --color-primary: #42a7ff;
  --color-primary-rgb: 66, 167, 255;
  --color-on-primary: white;
  --color-on-primary-rgb: 255, 255, 255;
  --color-primary-container: #c2e2ff;
  --color-primary-container-rgb: 194, 226, 255;
  --color-on-primary-container: #002342;
  --color-primary-rgb: 66, 167, 255;
  --color-error: #ba1a1a;
  --color-error-rgb: 186, 26, 26;
  --color-on-error: white;
  --color-on-error-rgb: 255, 255, 255;
  --color-error-container: #ea6969;
  --color-error-container-rgb: 234, 105, 105;
  --color-on-error-container: black;
  --color-error-rgb: 186, 26, 26;
  --color-background: white;
  --color-background-rgb: 255, 255, 255;
  --color-on-background: black;
  --color-on-background-rgb: 0, 0, 0;
  --color-background-rgb: 255, 255, 255;
  --color-surface: white;
  --color-surface-rgb: 255, 255, 255;
  --color-on-surface: #606060;
  --color-on-surface-rgb: 96, 96, 96;
  --color-surface-rgb: 255, 255, 255;
  --color-outline: #bfbfbf;
  --color-outline-rgb: 191, 191, 191;
  --color-on-outline: #404040;
  --color-on-outline-rgb: 64, 64, 64;
  --color-outline-rgb: 191, 191, 191;
  --color-surface-variant: #dfdfdf;
  --color-surface-variant-rgb: 223, 223, 223;
  --color-on-surface-variant: #202020;
  --color-on-surface-variant-rgb: 32, 32, 32;
  --color-surface-variant-rgb: 223, 223, 223;
}

body [data-color-scheme=dark],
html [data-color-scheme=dark] {
  --color-primary: #c2e2ff;
  --color-primary-rgb: 194, 226, 255;
  --color-on-primary: #004582;
  --color-on-primary-rgb: 0, 69, 130;
  --color-primary-container: #0067c2;
  --color-primary-container-rgb: 0, 103, 194;
  --color-on-primary-container: #c2e2ff;
  --color-primary-rgb: 194, 226, 255;
  --color-error: #f6bebe;
  --color-error-rgb: 246, 190, 190;
  --color-on-error: #120303;
  --color-on-error-rgb: 18, 3, 3;
  --color-error-container: #4a0a0a;
  --color-error-container-rgb: 74, 10, 10;
  --color-on-error-container: #ea6969;
  --color-error-rgb: 246, 190, 190;
  --color-background: black;
  --color-background-rgb: 0, 0, 0;
  --color-on-background: #dfdfdf;
  --color-on-background-rgb: 223, 223, 223;
  --color-background-rgb: 0, 0, 0;
  --color-surface: #404040;
  --color-surface-rgb: 64, 64, 64;
  --color-on-surface: #dfdfdf;
  --color-on-surface-rgb: 223, 223, 223;
  --color-surface-rgb: 64, 64, 64;
  --color-outline: hsl(0, 0%, 50%);
  --color-outline-rgb: 128, 128, 128;
  --color-on-outline: #f0f0f0;
  --color-on-outline-rgb: 240, 240, 240;
  --color-outline-rgb: 128, 128, 128;
  --color-surface-variant: #606060;
  --color-surface-variant-rgb: 96, 96, 96;
  --color-on-surface-variant: #f0f0f0;
  --color-on-surface-variant-rgb: 240, 240, 240;
  --color-surface-variant-rgb: 96, 96, 96;
}

.baseline-box {
  display: flex;
  align-items: baseline;
  gap: 1em;
  width: 100%;
}
.baseline-box.show-baseline p {
  position: relative;
}
.baseline-box.show-baseline p::after {
  width: 100vw;
  height: 1px;
  content: "";
  position: absolute;
  bottom: 0.35em;
  left: -280%;
  background-color: rgba(255, 0, 0, 0.23);
  pointer-events: none;
}

:root {
  --btn-font-size: 0.9em;
  --btn-sm: calc(var(--btn-font-size) - 0.2em);
  --btn-md: calc(var(--btn-font-size) + 0.2em);
  --btn-lg: calc(var(--btn-font-size) + 0.4em);
  --btn-radius: 4px;
}

.vw-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.8em;
  position: relative;
  text-transform: uppercase;
  line-height: normal;
  padding: 0 0.8em;
  border-radius: var(--btn-radius);
  font-size: 13px;
  border: none;
  height: 32px;
  white-space: nowrap;
  font-family: "Roboto", sans-serif;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  min-width: 32px;
}
.vw-btn::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: var(--btn-radius);
}
.vw-btn:hover::before {
  background-color: rgba(var(--color-on-background-rgb), 0.05);
}
.vw-btn:active::before {
  background-color: rgba(var(--color-on-background-rgb), 0.1);
}
