/* -------------------------------- 

Spacing

-------------------------------- */
:root {
  /* spacing values */
  --space-unit: 1em;
  --space-xxxs: calc(0.25 * var(--space-unit));
  --space-xxs: calc(0.375 * var(--space-unit));
  --space-xs: calc(0.5 * var(--space-unit));
  --space-sm: calc(0.75 * var(--space-unit));
  --space-md: calc(1.25 * var(--space-unit));
  --space-lg: calc(2 * var(--space-unit));
  --space-xl: calc(3.25 * var(--space-unit));
  --space-xxl: calc(5.25 * var(--space-unit));
  --space-xxxl: calc(8.5 * var(--space-unit));
  /* gaps */
  /* padding */
  /* Grid-32 */
  /* components padding */
  --component-padding: var(--space-sm);
}
:root .gap-xxxs {
  gap: calc(0.25 * var(--space-unit));
}
:root .gap-xxs {
  gap: calc(0.375 * var(--space-unit));
}
:root .gap-xs {
  gap: calc(0.5 * var(--space-unit));
}
:root .gap-sm {
  gap: calc(0.75 * var(--space-unit));
}
:root .gap-md {
  gap: calc(1.25 * var(--space-unit));
}
:root .p-xxxs {
  padding: calc(0.25 * var(--space-unit));
}
:root .p-xxs {
  padding: calc(0.375 * var(--space-unit));
}
:root .p-xs {
  padding: calc(0.5 * var(--space-unit));
}
:root .p-sm {
  padding: calc(0.75 * var(--space-unit));
}
:root .p-md {
  padding: calc(1.25 * var(--space-unit));
}
:root .grid-1 {
  min-height: 32px;
  min-width: 32px;
}

