  --color-primary-rgb: 66, 167, 255;
  --color-error: #ba1a1a;
  --color-error-rgb: 186, 26, 26;
  --color-on-error: white;
  --color-on-error-rgb: 255, 255, 255;
  --color-error-container: #ea6969;
  --color-error-container-rgb: 234, 105, 105;
  --color-on-error-container: black;
  --color-error-rgb: 186, 26, 26;
  --color-background: white;
  --color-background-rgb: 255, 255, 255;
  --color-on-background: black;
  --color-on-background-rgb: 0, 0, 0;
  --color-background-rgb: 255, 255, 255;
  --color-surface: white;
  --color-surface-rgb: 255, 255, 255;
  --color-on-surface: #606060;
  --color-on-surface-rgb: 96, 96, 96;
  --color-surface-rgb: 255, 255, 255;
  --color-outline: #bfbfbf;
  --color-outline-rgb: 191, 191, 191;
  --color-on-outline: #404040;
  --color-on-outline-rgb: 64, 64, 64;
  --color-outline-rgb: 191, 191, 191;
  --color-surface-variant: #dfdfdf;
  --color-surface-variant-rgb: 223, 223, 223;
