/* -------------------------------- 

Palette

-------------------------------- */
/* body[data-color-scheme="light"] {
  --paletteBackground: var(--color-surface-variant);
}

body[data-color-scheme="dark"] {
  --paletteBackground: var(--color-surface-variant);
}

body {
  --paletteBackground: var(--color-surface-variant);
} */
#app {
  max-height: calc(100% - 19px);
  display: flex;
  flex-direction: column;
  max-width: unset;
  height: 100%;
  padding: 0;
  gap: var(--space-xs);
  background: rgba(var(--color-surface-variant-rgb), 0.3);
}

.flex-wrapper {
  display: flex;
  flex-flow: row nowrap;
  gap: var(--space-xs);
}

.flex-wrapper-vert {
  display: flex;
  flex-flow: column nowrap;
  gap: var(--space-xs);
  /* box-shadow: 0 0 0 1px green; */
}

/* Utilities */
.vw-dialog-vertical-divider {
  width: 2px;
  background-color: var(--gray-3);
  margin: var(--space-xs) 0;
  height: calc(100% - 2 * var(--space-xs));
}

.nowrap {
  white-space: nowrap;
  flex-wrap: nowrap;
}

.render-image {
  background-size: cover;
  background-position: center;
}

.table-wrapper {
  flex: 1;
  background-color: var(--color-surface);
  position: relative;
  border-radius: 4px;
  overflow-y: auto;
  border-radius: var(--container-border-radius);
  box-shadow: 0 0 0 1px rgba(var(--color-on-surface-rgb), 0.12);
  border-radius: var(--container-border-radius);
}

.image-wrapper {
  box-shadow: inset 0 0 0 1px rgba(var(--color-on-surface-rgb), 0.12);
}

.responsive-row {
  height: calc(50% - var(--space-xs) / 2);
}

#app {
  container-type: inline-size;
  min-width: 0;
}

#content-wrapper {
  height: calc(100% - var(--space-xs) - 3 * var(--space-sm));
  display: flex;
  flex-flow: column nowrap;
  gap: var(--space-xs);
  /* box-shadow: inset 0 0 0 3px #000; */
}

#issue-list {
  flex: 1;
}

@container (max-width: 480px) {
  #id530825254l button {
    font-size: 0;
    padding: 0;
    transition: 0.2s;
  }
}
@container (max-width: 770px) {
  #toolbar-top > button,
  #id1234108975l button {
    font-size: 0;
    padding: 0;
    transition: 0.2s;
  }
  .responsive-row {
    flex-flow: row wrap;
  }
  .responsive-row.reverse {
    flex-flow: column-reverse nowrap;
  }
  .w-60, .w-40 {
    width: 100%;
  }
  .flex-wrapper-vert {
    flex: 1 1 100%;
    max-height: calc(50% - var(--space-xs) / 2);
  }
  #issue-list,
  #comment-box {
    max-height: 100%;
    overflow-y: auto;
  }
  .render-image {
    aspect-ratio: 16/9;
  }
  #content-wrapper > div:nth-child(1) {
    flex-flow: column nowrap;
    flex: 1 100%;
    max-height: calc(100% - var(--space-xs) - 4 * var(--space-sm));
  }
  #content-wrapper > div:nth-child(1) > div.col.w-40.flex-wrapper-vert,
  #render-preview {
    flex: 0;
  }
}
/* BCF-Comment-Table */
.vw-bcf-comment-content {
  max-width: 0;
  width: 100%;
}

.vw-bcf-comment-author-name {
  min-width: 170px;
}

.vw-bcf-comment-date {
  min-width: 95px;
}

.hide-me {
  display: none;
}

span.palette-title {
  position: absolute;
  width: 100%;
  color: var(--color-on-surface);
  display: flex;
  align-items: center;
  justify-content: center;
}

/* -------------------------------- 

Table

-------------------------------- */
.vw-table {
  max-width: 100%;
  border: none;
  border-collapse: collapse;
}
.vw-table thead th {
  color: rgba(var(--color-on-surface-rgb), 0.6);
}
.vw-table th {
  background: var(--color-surface);
  font-weight: 400;
  text-transform: uppercase;
}
.vw-table td, .vw-table th {
  color: var(--color-on-surface);
  padding: 0.5em;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  border: none;
  text-align: left;
}
.vw-table td:not(.vw-table-fixed-td td), .vw-table th:not(.vw-table-fixed-td th) {
  max-width: 0;
}
.vw-table tr {
  border-bottom: 1px solid rgba(var(--color-on-surface-rgb), 0.12);
}

/* -------------------------------- 

Input

-------------------------------- */
.input_search {
  font-size: 100%;
  padding-left: var(--space-md);
  border-radius: 100px;
  border: none;
  box-shadow: 0 0 0 1px rgba(var(--color-on-surface-rgb), 0.1);
  background-color: var(--color-surface);
  height: 32px;
}
.input_search:focus {
  outline: none;
  box-shadow: 0 0 0 1px rgba(var(--color-on-surface-rgb), 0.12), 0 0 0 2px rgba(var(--color-on-surface-rgb), 0.05);
  border: none;
}
.input_search:focus::placeholder {
  color: rgba(var(--color-primary-rgb), 0.5);
}

/* -------------------------------- 

Buttons

-------------------------------- */
/* @import'colors';

$darkMode: true;

@mixin darkMode {
  @if($darkMode) {
    @media(prefers-color-scheme: dark) {
      @content;
    }
  }
} */
html [data-color-scheme=light] {
  --popoverContainerBoxshadow: 0px 0px 0px 0.5px rgba(0,0,0,0.15), 0px 2px 25px 0px rgba(0,0,0,0.25);
  --accentBackgroundColor: #dedede;
  --color-text: #1c1c1c;
  --testBackground: #000;
}

.palette-wrapper {
  resize: both;
  overflow: hidden;
  position: absolute;
  max-width: calc(100% - 4em) !important;
  max-height: calc(100vh - 6em) !important;
  left: 50%;
  top: 0;
  transform: translate(-50%, 0px);
  background: var(--dialog-background);
  border-radius: 1px;
  box-shadow: var(--popoverContainerBoxshadow);
}

.palette-title {
  display: flex;
  min-height: 20px;
  height: 19px;
  overflow: hidden;
  position: relative;
}

.title-edge {
  position: absolute;
  inset: 0 0 -2px 0;
  padding: 0 4px 1px 4px;
  box-shadow: var(--popoverContainerBoxshadow);
  background-color: var(--accentBackgroundColor);
  color: var(--color-text) !important;
  display: flex;
  gap: 4px;
  flex-flow: row nowrap;
  align-items: center;
  font-size: 76%;
  border-radius: 1px 1px 0 0;
}

.cs-right-area {
  color: var(--md-sys-color-on-surface);
  flex: 1;
  padding: 2em;
  min-height: 2000px;
}

.highlightrect {
  display: none;
}

/* Styleguide Pane */
.cs-sample-block {
  border-radius: 4px;
  position: relative;
  overflow: hidden;
}
.cs-sample-block::after {
  content: "";
  border-radius: 4px;
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.2);
  width: 100%;
  position: absolute;
  inset: 0;
  pointer-events: none;
}

.cs-element-container {
  padding: 24px;
  background: var(--dialog-background);
  /* border-bottom: 1px solid var(--mdc-theme-on-surface, #EBECF0); */
  display: flex;
  align-items: baseline;
  gap: 1em;
}
.cs-element-container :has(+ div) {
  border-radius: 3px;
}

.cs-color {
  flex-flow: row nowrap;
  padding: 0;
  gap: 0;
  overflow: hidden;
}

/* Syntax Highlighting */
.wp_syntax {
  border: none;
  background: transparent !important;
}

div.code {
  background: transparent !important;
  padding: 1.8em;
  overflow-x: auto;
}
div.code:empty {
  display: none;
}

.code-highlight {
  background: rgba(0, 0, 0, 0.1);
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.5);
  border-radius: 2px;
  padding: 2px 4px;
  font-size: 90%;
}

.swatches {
  flex: auto;
  margin: 20px;
}

.swatch-title {
  padding: 12px;
  text-transform: capitalize;
  min-height: 65px;
  flex: 1 1 11.6%;
  font-weight: 400;
  flex-flow: column wrap;
  color: #fff;
}

.color-swatch:nth-child(n+7) {
  color: #fff;
}

.color-swatch {
  padding: 12px;
  font-weight: bold;
  flex: 1 1 auto;
  min-height: 65px;
  flex-flow: column wrap;
  text-transform: capitalize;
  flex: 1 1 0;
}

.iframe-button {
  min-width: 750px !important;
  height: 750px !important;
  border: none;
  scale: 0.3333333;
  position: absolute;
  left: -100%;
  top: -100%;
  pointer-events: none;
}

.iframe-button-wrapper {
  width: 250px;
  height: 250px;
  overflow: hidden;
  position: relative;
}

.back-button {
  position: fixed;
  left: 10px;
  top: 38px;
  background-color: rgb(var(--color-primary-rgb), 0.3);
  height: 48px;
  width: 48px;
  border-radius: 50%;
  z-index: 999;
  /* box-shadow: inset 0 0 0 1px rgba(0,0,0,0.1); */
}
.back-button:hover {
  background-color: rgb(var(--color-primary-rgb), 0.6);
  transition: 0.2s;
}

